/* Center everything */
.login-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #74b9ff, #0984e3);
  font-family: "Poppins", sans-serif;
}

/* Card styling */
.login-card {
  background: white;
  border-radius: 16px;
  padding: 40px 35px;
  width: 360px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  text-align: center;
  animation: fadeIn 0.6s ease-in-out;
}

/* Title and subtitle */
.login-title {
  margin-bottom: 10px;
  color: #2d3436;
}

.login-subtitle {
  color: #636e72;
  font-size: 0.9rem;
  margin-bottom: 25px;
}

/* Form elements */
.form-group {
  text-align: left;
  margin-bottom: 20px;
}

.form-group label {
  font-weight: 500;
  color: #2d3436;
  display: block;
  margin-bottom: 6px;
}

.form-group input {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #dcdde1;
  border-radius: 8px;
  outline: none;
  font-size: 0.95rem;
  transition: border-color 0.3s;
}

.form-group input:focus {
  border-color: #0984e3;
}

/* Button */
.login-btn {
  width: 100%;
  padding: 12px;
  background: #0984e3;
  border: none;
  border-radius: 8px;
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.3s;
}

.login-btn:hover {
  background: #74b9ff;
}

/* Footer */
.login-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  font-size: 0.9rem;
  color: #636e72;
}

.signup-link {
  color: #0984e3;
  text-decoration: none;
  font-weight: 500;
}

.signup-link:hover {
  text-decoration: underline;
}

/* Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
